export function computeSmaSeries(series, period) {

    return series.rollingWindow(period)
        .asPairs()
        .select(function (pair) {
            var window = pair[1];
            return [window.getIndex().last(), window.average()];
        })
        .asValues()
        ;
};

export function dataFrameToHighstockOHLC(dataFrame) {

    var it = dataFrame.getIterator();

    var output = [];
    while (it.moveNext()) {
        var pair = it.getCurrent();
        var index = pair[0];
        var row = pair[1];
        output.push([
            index.getTime(),
            row.open,
            row.high,
            row.low,
            row.close,
        ]);
    }

    return output;
};
export function seriesToHighstock(series) {

    var it = series.getIterator();

    var output = [];
    while (it.moveNext()) {
        var pair = it.getCurrent();
        var index = pair[0];
        var value = pair[1];
        output.push([
            index.getTime(),
            value,
        ]);
    }

    return output;
};