import React from 'react';
import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';
import './css/App.css';
import {loadChart} from './js/chart.js'
class App extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      options : null,
      company : "MSFT",
      sma_period : 30,
      Highcharts : Highcharts,
      window : {
        width :  800,
        hieght : 182
      }
    }
  }
  updateDimensions() {
      this.setState(prev => {
        let jasper = Object.assign({}, prev.jasper);  // creating copy of state variable jasper
        jasper.width = window.width;
        jasper.height = window.heigt-50;
        return {jasper}
      })
  }
  destroyChart = function () {
    if (this.state.Highcharts) {
        this.state.Highcharts.destroy();
    }
  };
  reloadChart = function(){         
    this.destroyChart();
    this.loadChart();
  }
  companentDidMount(){
    this.updateDimensions();
    window.addEventListener("resize", this.updateDimensions.bind(this));
  }
  componentWillMount(){
    this.loadChart = loadChart.bind(this);
    window.removeEventListener("resize", this.updateDimensions.bind(this));
    this.loadChart();
  }
  render(){
    return (
      <div className="App">
          <div className="company_input">
            <button onClick={this.reloadChart}>Search</button>
            <input
              name="company"
              type="text"
              value={this.state.company}
              onClick={this.reloadChart}
              onChange={(e)=>{this.setState({[e.target.name]:e.value})}}
            />
          </div>
  
          {/* <div className="SMA_input"> 

          </div> */}
          <HighchartsReact 
            highcharts={this.props.Highcharts} 
            options={this.props.options} 
            constructorType={'stockChart'} 
          />
      </div>
    );
  }
}
export default App;
